.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  width: 36px;
  height: 52px;
  line-height: 0px;
  font-size: 0px;
  cursor: pointer;
  top: 50%;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  padding: 0;
  border: none;
  outline: none;
}

.slick-prev:hover, .slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  opacity: 0.5;
}

.slick-prev {
  left: 25px;
  z-index: 99999;
  background-image: url(/img/arr/prev.png);
}

.slick-next {
